.filtro {
  max-width: 100%;
  border: 4px solid #FFFFFF;
  border-radius: 2rem;
  padding: 2rem;
  margin: 4rem auto;
  display: flex;
  flex-direction: column;
}
.filtro select, .filtro input {
  width: 70%;
  margin: 0.1rem auto;
  font-size: 1.4rem;
  text-align: center;
  color: #FFFFFF;
  background-color: #396c8a;
  border-radius: 6px;
  border: 2px solid rgba(0, 3, 182, 0.7803921569);
}
.filtro select:hover, .filtro input:hover {
  color: #1e1e1e;
  background-color: #FFFFFF;
  cursor: pointer;
}
.filtro input[type=submit] {
  display: inline !important;
  margin: auto !important;
}
@media (min-width: 1024px) {
  .filtro {
    flex-direction: row;
    justify-content: space-between;
    max-width: 90%;
  }
  .filtro select, .filtro input {
    width: auto;
  }
  .filtro .input-buscador {
    width: 15rem;
    background-color: #FFFFFF;
    color: #1e1e1e;
  }
}

.trabajadores {
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(2, 1fr);
}
.trabajadores .trabajador {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 5%;
  border-radius: 0.5rem;
  border: 1px solid rgb(170, 186, 194);
  height: 100%;
  transition: all 300ms ease;
}
.trabajadores .trabajador:hover {
  transform: scale(1.05);
}
.trabajadores .trabajador p {
  text-align: center;
  line-height: 1.8rem;
  font-size: 1.2rem;
}
@media (min-width: 768px) {
  .trabajadores {
    display: grid;
    gap: re4m;
    grid-template-columns: repeat(3, 1fr);
  }
  .trabajadores .trabajador p {
    font-size: 1.6rem;
  }
}

.confirmar-cuenta {
  height: 70vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.inmuebles {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(2, 1fr);
  margin-bottom: 60px;
}
.inmuebles .inmueble {
  border-radius: 8px;
  height: auto;
  padding: 20px 10px;
  border: 4px solid #022D36;
  transition: all 300ms ease;
}
.inmuebles .inmueble img {
  width: 100%;
  height: auto;
  border-radius: 1rem;
  margin-bottom: 1rem;
}
.inmuebles .inmueble:hover {
  cursor: pointer;
  transform: scale(1.05);
}
.inmuebles .inmueble .info-inmueble {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
.inmuebles .inmueble .info-inmueble .direccion {
  color: #1e1e1e;
  font-weight: 400;
  font-style: italic;
  padding: 1rem;
  text-align: center;
  font-size: 1.1rem;
  line-height: auto;
  height: 9rem;
}
.inmuebles .inmueble .info-inmueble .direccion::before {
  content: "";
  display: block;
  height: 4px;
  width: 100%;
  background-color: #1F456E;
  margin: auto;
  border-radius: 8px;
  margin-bottom: 16px;
}
.inmuebles .inmueble .info-inmueble .info-inferior {
  width: 100%;
  margin: auto;
}
.inmuebles .inmueble .info-inmueble .info-inferior .campo-info, .inmuebles .inmueble .info-inmueble .info-inferior .estado {
  margin: 0 auto;
  text-align: center;
  font-size: 1.2rem;
  font-weight: 700;
}
.inmuebles .inmueble .info-inmueble .info-inferior .precio {
  margin: 0 auto;
  margin-top: 1rem;
  text-align: center;
  font-size: 1.2rem;
  font-weight: 700;
  color: #022D36;
}
.inmuebles .inmueble .info-inmueble .info-inferior .estado {
  color: #FFFFFF;
  font-weight: 700;
  background-color: #1F456E;
  margin-bottom: 0.5rem;
  border-radius: 8px;
}
.inmuebles .inmueble .info-inmueble .info-inferior .campo-info {
  font-weight: 400;
}
@media (min-width: 1024px) {
  .inmuebles {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(4, 1fr);
  }
  .inmuebles .inmueble .info-inmueble .direccion {
    font-size: 1.3rem;
  }
  .inmuebles .inmueble .info-inmueble .info-inferior {
    width: 98%;
    margin: auto;
  }
  .inmuebles .inmueble .info-inmueble .info-inferior .precio, .inmuebles .inmueble .info-inmueble .info-inferior .campo-info, .inmuebles .inmueble .info-inmueble .info-inferior .estado {
    margin: 0 auto;
    text-align: center;
    font-size: 1.4rem;
  }
}

.buscador {
  margin: 1rem auto;
}

.beneficios {
  margin: 1rem 0;
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(2, 1fr);
  width: 100%;
}
.beneficios .beneficio {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
}
.beneficios .beneficio:last-of-type {
  border-right: none;
}
.beneficios .beneficio img {
  height: 35%;
  width: 35%;
  margin: auto;
  border-radius: 0;
}
.beneficios .beneficio p {
  font-size: 1.2rem;
