}

.opciones {
  margin-top: 1.2rem;
  display: grid;
  gap: 0.5rem;
  grid-template-columns: repeat(2, 1fr);
}
.opciones a {
  font-size: 1rem;
  width: 100%;
  height: auto;
  margin: auto;
  color: #1e1e1e;
}
.opciones a:hover {
