.tabla table th, .tabla table td {
  border: 1px solid rgb(170, 186, 194);
  text-align: center;
  padding: 0.5rem 1rem;
  font-size: 1.2rem;
}
.tabla table td div {
  display: grid;
  grid-template-columns: 30% 70%;
